.aboutus {
  width: 100vw;
  height: inherit;
  overflow-x: hidden;
  background-image: url("https://images.pexels.com/photos/6156383/pexels-photo-6156383.jpeg?auto=compress&cs=tinysrgb&w=600");
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  padding-top: 25px;
  padding-bottom: 25px;

  .main {
    margin-bottom: 100px;
    .h {
      h1 {
        text-align: center;
        margin-bottom: 5px;
      }
      h2 {
        text-align: center;
      }
    }
    .company {
      background-color: aliceblue;
      margin-top: 5%;
      width: 100vw;
      height: inherit;
      display: flex;
      justify-content: space-around;

      .r {
        display: flex;
        flex-direction: column;
        width: 30vw;
        justify-content: space-around;
        img {
          width: 100%;
        }
      }
    }
  }

  .story {
    width: 90vw;
    margin: auto;
    margin-bottom: 100px;

    .hs {
      margin-bottom: 20px;
      h1 {
        text-align: center;
        margin-bottom: 5px;
      }
      h2 {
        text-align: center;
      }
    }
    .bgimg {
      img {
        width: 100%;
      }
    }

    .storydesc {
      margin-top: 25px;
      font-size: 20px;
      line-height: 2rem;
      word-wrap: break-word;
      letter-spacing: 2px;
      text-align: justify;
    }
  }

  #h1t {
    text-align: center;
  }
  .team {
    margin-top: 5%;
    width: 100vw;
    margin-left: 1%;
    display: flex;
    justify-content: space-around;
    max-width: 100%;
    flex-wrap: wrap;
    // background-color: wheat;

    .teammeamber {
      overflow: hidden;
      background-color: antiquewhite;
      color: black;
      border: 5px gray solid;
      cursor: pointer;
      margin-right: 1%;
      margin-top: 2%;

      img {
        width: 300px;
        height: 400px;
        &:hover {
          border-radius: 40%;
          border: 5px black solid;
        }
      }
      .occu {
        padding: 10px;
      }
    }
  }
}
