@media (min-width: 800px) {
  .product {
    padding: 10px 50px;
    display: flex;
    gap: 30px;
    background-color: antiquewhite;

    .left {
      flex: 1;
      display: flex;

      .images {
        flex: 1;

        img {
          width: 100%;
          height: 150px;
          object-fit: cover;
          cursor: pointer;
          margin-bottom: 5px;
        }
      }

      .mainImg {
        flex: 5;
        display: flex;

        img {
          width: 100%;
          max-height: 800px;
          object-fit: cover;
          margin-left: 10%;
        }
      }
    }

    .right {
      padding: 10px 50px;
      flex: 1;
      display: flex;
      flex-direction: column;
      gap: 20px;

      .price {
        font-size: 30px;
        font-weight: 500;
        color: blue;
      }

      .size {
        display: flex;
        gap: 100px;
        align-items: center;
      }

      .sizes {
        display: flex;
        gap: 20px;

        button {
          cursor: pointer;
          font-size: 1.2rem;
          background-color: grey;
          color: white;
          border-radius: 50%;
          width: 5vw;
          height: 10vh;
        }
      }

      .quantity {
        margin-top: 20px;
        display: flex;
        gap: 10px;
        font-size: 2rem;

        button {
          cursor: pointer;
          font-size: 1.2rem;
          width: 40px;
        }
      }

      .buttons {
        margin-top: 20px;
        display: flex;
        gap: 20px;
        button {
          font-size: 1.2rem;
          padding: 10px;
          background-color: yellow;
          cursor: pointer;
        }
      }

      .buyButton {
        margin-top: 20px;
        display: flex;
        button {
          font-size: 1.5rem;
          padding: 10px;
          cursor: pointer;
          background-color: red;
          color: aliceblue;
          width: 200px;
          padding-left: 15px;
          padding-right: 15px;
          border-radius: 20px;
        }
      }

      .info {
        margin-top: 50px;
        display: flex;
        flex-direction: column;
        gap: 20px;
      }
    }
  }
}
