.contact {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 90vh;
  background-color: white;
  margin: auto;
  overflow: hidden;

  .left {
    width: 50%;
    height: 100%;

    img {
      width: 80%;
      height: 80%;
      margin-top: 5%;
      margin-left: 5%;
      border-radius: 5%;
    }
  }

  .right {
    width: 50%;
    height: 100%;
    background: wheat;
    .top {
      h1 {
        font-size: 3rem;
        margin-top: 2%;
        margin-left: 5%;
      }
    }

    .bottom {
      border-top: 2px black solid;
      border-left: 2px black solid;
      margin-top: 3%;

      overflow: hidden;
      display: flex;

      .contactLeft {
        margin-top: 5%;
        margin-left: 5%;
        width: 45%;
        height: 100%;
        font-size: 2rem;

        form label input {
          margin-bottom: 15px;
          border: none;
          background-color: wheat;
          font-size: 1rem;
          border-bottom: 4px black solid;
          &:hover {
            outline: none;
          }
        }

        form button {
          font-size: 1.5rem;
          background: #000;
          color: white;
          width: 80%;
          border-radius: 20px;
          padding: 10px;
          margin: auto;
          margin-top: 10%;
          margin-bottom: 5%;
          &:hover {
            cursor: pointer;
          }
        }
      }

      .contactRight {
        margin-top: 5%;
        margin-left: 2%;
        width: 48%;
        height: 100%;
        display: flex;
        flex-direction: column;

        .details {
          margin-bottom: 25%;
          h2 {
            font-size: 2rem;
          }
          h3 {
            font-size: 1.5rem;
          }
        }

        .address {
          margin-bottom: 25%;
          h2 {
            font-size: 2rem;
          }
          h3 {
            font-size: 1.5rem;
          }
        }

        .social {
          display: flex;
          gap: 20px;
          overflow: hidden;

          h2 {
            font-size: 2rem;
            &:hover {
              color: #777;
            }
          }
        }
      }
    }
  }
}
